@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
:root {
  --main-bg-color: #f8f7f5;
  --main-dark-bg-color: #0d1219;
  --head-bg-color: #313036;
  --head-dark-bg-color: #202c3d;
}
body {
  margin: unset;
}
html,
body,
#root,
.App {
  height: 100%;
}
.App {
  display: grid;
  grid-template: auto / 1fr 3fr;
  grid-template-areas: "s c c c";
  font-family: "Roboto", sans-serif;
}
.sidebar {
  background-color: #f8f7f5;
  background-color: var(--main-bg-color);
  text-align: center;
  color: black;
  grid-area: s;
  display: grid;
  grid-template: repeat(4, auto) / 1fr;
  grid-template-areas:
    "n"
    "p"
    "d";
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.content {
  grid-area: c;
  overflow-y: scroll;
}
.pic {
  padding: 5px;
  grid-area: p;
}
.name {
  grid-area: n;
}
.description {
  grid-area: d;
}
a {
  color: black;
}
a:hover {
  text-decoration: none;
}
.pic img {
  max-width: 50%;
  border-radius: 30%;
}
.experience {
  grid-gap: 5%;
  display: grid;
  grid-template: auto / repeat(4, auto);
}
.elevation {
  box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2),
    0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
}
.info {
  grid-column-start: 2;
  padding: 2%;
}
.chip {
  display: inline-block;
  background: #e0e0e0;
  padding: 0 12px;
  border-radius: 32px;
  font-size: 13px;
}
.chip:hover {
  background: #ccc;
  cursor: pointer;
}
.title {
  text-align: center;
  grid-column-start: 2;
}
@media screen and (min-width: 600px) and (max-width: 800px) {
  .experience {
    grid-template: auto / auto;
  }
  ul {
    padding-left: 15%;
  }
  .content {
    padding-right: 5%;
  }
}
@media screen and (max-width: 600px) {
  .App {
    grid-template: auto / auto;
    grid-template-areas: "s" "c";
  }
  .content {
    overflow-y: unset;
  }
  .sidebar {
    grid-template: none;
  }
  .experience {
    grid-gap: 2%;
  }
}
@media (prefers-color-scheme: dark) {
  .sidebar {
    background-color: #0d1219;
    background-color: var(--main-dark-bg-color);
    color: white;
  }
  a {
    color: white;
  }
  .content {
    background-color: #202c3d;
    background-color: var(--head-dark-bg-color);
  }
}

